<template>
	<view class="news-detail-page">
		<template v-if="newsItem">
			<view class="news-header">
				<view class="news-title">{{ newsItem.title }}</view>
				<view class="news-desc-info">
					<view class="news-publish-time">发布于{{ newsItem.publishTime }}</view>
					<view class="news-view-count-info">
						<image class="news-view-icon" src="/static/img/icon_browse.png" mode="widthFix"></image>
						<text class="news-view-count">{{ newsItem.viewCount }}</text>
					</view>
				</view>
			</view>
						
			<view class="news-content">
				<u-parse :content="newsItem.newsContent"></u-parse>
			</view>
						
		</template>
	</view>
</template>

<script>
	import UParse from '@/components/u-parse/u-parse.vue'
	export default {
		name: 'NewsDetailPage',
		components: {
			UParse
		},
		data() {
			return {
				newsItem: null
			}
		},
		onLoad(option) {
			console.log(option)
			this.getNewsContent(option.newsId)
		},
		methods: {
			getNewsContent(newsId) {
				if (!newsId) {
					return false
				}
				
				this.newsItem = {
					id: 1,
					title: '全国货物物流总体有序运行 持续恢复',
					publishTime: '2022.07.19',
					viewCount: 9999,
					newsContent: '<img src="https://images.unsplash.com/photo-1658827045635-d8592ff6a4ec?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1632&q=80"/>xxxxxxxxxx',
					img: 'https://images.unsplash.com/photo-1658827045635-d8592ff6a4ec?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1632&q=80'
				}
			}
		}
	}
</script>

