	.news-detail-page {
		padding: 0 30upx 30upx;
	}
	
	.news-header {
		padding: 26upx 0 30upx;
		border-bottom: 2upx solid $border-color;
	}
	
	.news-title {
		font-size: 34upx;
		line-height: 48upx;
		font-weight: 500;
	}
	
	.news-desc-info {
		flex-shrink: 0;
		@include flex;
		justify-content: space-between;
		align-items: center;
		margin-top: 30upx;
	}
	
	.news-publish-time {
		color: #A3A7B3;
		font-size: 24upx;
		line-height: 32upx;
	}
	
	.news-view-count-info {
		@include flex-c-center;
	}
	
	.news-view-icon {
		width: 32upx;
		height: 32upx;
		margin-right: 6upx;
	}
	
	.news-view-count {
		color: #A3A7B3;
		line-height: 32upx;
		font-size: 24upx;
	}
	
	.news-content {
		margin-top: 30upx;
		
